	font-family: 'Roboto Slab', sans-serif;
	font-weight: 400;
	color: #808080;
	font-size: 13px;
	line-height: 20px;
	position: relative;
}
:focus {
	outline: none !important;
}
h1, h2, h3, h4, h5, h6 {
	color: #808080;
	font-family: 'Arimo', arial;
}
/* default font size */
.fa {
	font-size: 13px;
}
/* Override the bootstrap defaults */
h1 {
	font-size: 14px;
	text-transform: uppercase;
}
h2 {
	font-size: 14px;
	text-transform: uppercase;
}
h3 {
	font-size: 13px;
}
h4 {
	font-size: 12px;
}
h5 {
	font-size: 11px;
}
h6 {
	font-size: 10px;
}
a {
	color: #808080;
	text-decoration: none;
}
a:hover {
	text-decoration: none;
	color: #fe5723;
}
legend {
	font-size: 14px;
	font-family: 'Arimo', arial;
	text-transform: uppercase;
	padding: 5px 0px 5px;
	display: block;
	width: 100%;
	margin-bottom: 20px;
	color: #666;
	border: 0;
	border-bottom: 1px solid #e5e5e5;
}
label {
	font-size: 12px;
}
.box {
	margin: 0 -10px;
}
select.form-control, textarea.form-control, input[type="text"].form-control, input[type="password"].form-control, input[type="datetime"].form-control, input[type="datetime-local"].form-control, input[type="date"].form-control, input[type="month"].form-control, input[type="time"].form-control, input[type="week"].form-control, input[type="number"].form-control, input[type="email"].form-control, input[type="url"].form-control, input[type="search"].form-control, input[type="tel"].form-control, input[type="color"].form-control {
	font-size: 12px;
	-moz-appearance: none;
	appearance: none;
	-webkit-appearance: none;
}
.input-group input, .input-group select, .input-group .dropdown-menu, .input-group .popover {
	font-size: 12px;
}
.input-group .input-group-addon {
	font-size: 12px;
	height: 30px;
}
/* Fix some bootstrap issues */
span.hidden-xs, span.hidden-sm, span.hidden-md, span.hidden-lg {
	display: inline;
}
.nav-tabs {
	margin-bottom: 15px;
}
div.required .control-label:before {
	content: '* ';
	color: #F00;
	font-weight: bold;
}
/* Gradient to all drop down menus */
.dropdown-menu li > a:hover {
	text-decoration: none;
	color: #ffffff;
	background-color: #0e1011;
	background-image: -webkit-gradient(linear, left top, left bottom, from(#3b5998), to(#1f90bb));
	background-image: linear-gradient(to bottom, #3b5998, #1f90bb);
	background-repeat: repeat-x;
}
/* top */
#top .container {
	padding: 0 20px;
}
#top #currency .currency-select {
	text-align: left;
}
#top #currency .currency-select:hover {
	text-shadow: none;
	color: #ffffff;
	background-color: #3b5998;
	background-image: -webkit-gradient(linear, left top, left bottom, from(#3b5998), to(#1f90bb));
	background-image: linear-gradient(to bottom, #3b5998, #1f90bb);
	background-repeat: repeat-x;
}
#top .btn-link, #top-links a {
	color: #aaabab;
	text-decoration: none;
	padding: 2px 8px 0;
	display: inline-block;
}
#top-links li .fa {
	font-size: 12px;
	margin: 0 5px 0 0;
}
#top-links > li {
	padding: 0 11px;
}
#top .btn-link:hover, #top-links a:hover {
	color: #fff;
}
#top-links .dropdown-menu a {
	text-shadow: none;
}
#top-links .dropdown-menu a:hover {
	color: #FFF;
}
#top .btn-link strong {
	font-size: 14px;
	line-height: 14px;
}
#top-links {
	margin: 6px 0;
	float: none;
	display: inline-block;
	vertical-align: top;
}
#top-links ul {
	margin: 0;
	top: 27px;
}
#top-links a + a {
	margin-left: 15px;
}
/* CSS for Header Start */
.header-top {
	background: #333 none repeat scroll 0 0;
}
.language .btn.btn-link.dropdown-toggle {
	padding-left: 0;
}
.language, .currency {
	float: left;
}
header {
	background: #fff none repeat scroll 0 0;
}
.respo-header-middle, .top-toggle {
	display: none;
}
.header-inner {
	text-align: center;
	padding: 30px 0;
}
.header-inner .header-left {
	width: auto;
	padding: 0;
	margin: 13px 0;
}
.header-inner .header-middle {
	width: auto;
	padding: 0;
	display: inline-block;
	float: none;
}
.header-inner .header-right {
	padding: 0;
	float: right;
	width: auto;
}
/* logo */
#logo {
	margin: 0;
}
.shipping-img, .returns-img {
	background: url(data:image/png;base64,) no-repeat scroll 0 -85px;
	height: 40px;
	width: 50px;
	float: left;
}
.returns-img {
	background-position: 0 0;
}
.returns, .shipping {
	text-align: left;
	font-family: arimo;
	padding: 0;
}
.returns-text, .shipping-text {
	color: #000;
	font-family: arimo;
	font-size: 17px;
	overflow: hidden;
	font-weight: bold;
}
.returns-detail, .shipping-detail {
	float: left;
	clear: left;
	font-size: 13px;
	font-weight: normal;
	color: #000;
}
/* search */
.header-right #search .input-lg {
	height: 33px;
	line-height: 20px;
	border-radius: 0;
	width: 145px;
	border: 0;
	background-color: #fff;
}
.header-right #search .btn-lg {
	line-height: 21px;
	padding: 6px 6px 6px 0;
	text-shadow: none;
	background: #fff;
	border-radius: 0;
	border: 0;
	color: #666;
	width: 20px;
}
#search {
	margin-bottom: 10px;
}
#search .input-lg {
	height: 40px;
	line-height: 20px;
	padding: 0 10px;
}
#search .btn-lg {
	font-size: 15px;
	line-height: 18px;
	padding: 10px 35px;
	text-shadow: 0 1px 0 #FFF;
}
/* Cart */
#cart {
	margin-bottom: 10px;
}
#cart > .btn {
	font-size: 12px;
	line-height: 18px;
	color: #FFF;
}
#cart > .btn:hover .fa {
	color: #666;
}
#cart .img-thumbnail {
	max-width: none;
	width: 50px;
}
.header-right #cart > .btn {
	font-size: 13px;
	line-height: 18px;
	color: #000;
	background: none;
	border: 0;
	text-shadow: none;
	padding: 8px 5px 10px;
	margin: 0 auto;
	width: auto;
}
.header-right #cart {
	float: right;
	margin: 0;
}
.header-right #cart .dropdown-menu {
	margin: 0;
}
.header-right .fa-caret-down {
	padding: 0px 0px 0 10px;
	color: #aaabab;
}
.header-right .fa-caret-down:hover {
	color: #fff;
}
